<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="padding-t_10 p-0" id="Myprofile">
            <div class="left-menu-tab pt-2">
                <div class="col-md-12 p-0 ">
                    <div class="theme-primary partition">
                        <span class="p-name text-white">{{ $t('UserProfile') }} </span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab  nav-tabs">
                    <li v-for="tab in tabs" :key="tab.id" @click="activeTab = tab.id, changeTab(tab.id)"
                        :class="{ 'nav-item': true, 'active': activeTab === tab.id }">
                        <span :class="{ 'nav-link left-menu': true, 'active': activeTab === tab.id }" :title="tab.title">
                            <i :class="tab.icon" class="p-1"  ></i> {{ tab.title }}</span>
                    </li>
                </ul>

            </div>
            <div class="right-content">
                <div id="ReturnRequestHeading" class=" mt-2 theme-primary partition  p-actions-expand" >
                    <span class="p-name text-white"> {{ tabHeaderText }}</span>

                </div>
                <div class="col-md-12 p-0" v-for="tab in tabs" :key="tab.id" v-show="activeTab === tab.id">
                    <GeneralSetting v-if="isSettingsVisible && tab.id == 1"> </GeneralSetting>
                    <Communication v-if="isCommunicationVisible && tab.id == 2" :GetConfiguration="GetConfiguration" IsClient="User"></Communication>
                    <Address v-if="isAddressVisible && tab.id == 3" :UserId="userId" IsClient="User">
                    </Address>
                    <ContactNumber v-if="isShowContactNumber && tab.id == 4" @userTypeId="receiveUserTypeId"
                         :UserId="userId" IsClient="User"></ContactNumber>
                    <TeamStructure v-if="isTeamStructureVisible && tab.id == 5" :UserId="userId" :reqform="'myprofile'"></TeamStructure>
                    <SecurityQuestions v-if="isSecurityQuestions && tab.id == 6"> </SecurityQuestions>
                </div>
            </div>
<FaceRecognizer @close="showFaceRecognizerModal = false" :id="userIdForfaceCapture"  :name="GetUserInfo.Name" v-if="showFaceRecognizerModal" @statusUpdate="updateRecord($event)"/>
        </div>

    </section>
</template>
<script>
import DataService from '../../services/DataService';
import GeneralSetting from './GeneralSetting.vue';
import Communication from "../Users/Communication.vue";
import Address from "../Users/Address.vue";
import ContactNumber from '../../components/Modals/ContactNumber.vue';
import TeamStructure from "../Users/TeamStructure.vue";
import SecurityQuestions from './SecurityQuestions.vue';   
import FaceRecognizer from '../Users/FaceCapture.vue'; 
export default {
    components:{
        GeneralSetting,Communication,Address,ContactNumber,TeamStructure,SecurityQuestions,FaceRecognizer
    },
    data(){
        return{
            userIdForfaceCapture:null,
            showFaceRecognizerModal: false,
            activeTab: 1,
            isSettingsVisible: false,
            isAddressVisible: false,
            isShowContactNumber:false,
            isTeamStructureVisible:false,
            isSecurityQuestions:false,
            tabHeaderText: "",
            tabs: [{
                    title: "GeneralSettings",
                    icon: "fa fa-cogs",
                    id: 1
                },
                {
                    title: "CommunicationDetails",
                    icon: "fa fa-phone",
                    id: 2
                },
                {
                    title: "AdditinalAddresses",
                    icon: "fa fa-address-book",
                    id: 3
                },
                {
                    title: "ContactNumbers",
                    icon: "fa fa-file",
                    id: 4
                    
                },
            {
                    title: "FaceCapture",
                    icon: "fa fa-camera",
                    id: 7
                    
                }],
                userId: null,userType: null,
                Headerstext: '',
                isLoading:false,
                GetConfiguration: {},
        }
    },
    async created() {
        var vm = this;
        vm.userId=vm.GetUserInfo.ID
        vm.isSettingsVisible = true;
        vm.tabHeaderText = 'General Settings';
        if(vm.GetUserInfo.UserType!='CL' && vm.GetUserInfo.UserType!='SA'){
            vm.tabs.push({
                title: "Team Structure",
                icon: "fa fa-sitemap",
                id: 5
            })
        }
        await vm.GetPasswordPolicyByCompanyId();
       
    },
    methods: {
        updateRecord(event) {
          this.showFaceRecognizerModal = false; 
        },
        changeTab(tab) {
            var vm = this; 
            if(tab == 7){
                vm.userIdForfaceCapture = vm.userId;
                
            }
            

            this.isSettingsVisible = tab === 1;
            this.isCommunicationVisible = tab === 2;
            this.isAddressVisible = tab === 3;
            this.isShowContactNumber = tab === 4;
            this.isTeamStructureVisible = tab === 5;
            this.isSecurityQuestions= tab == 6

            switch (tab) {

                case 1:
                    vm.isSettingsVisible = true;
                    vm.tabHeaderText = 'General Settings';
                    break;
                    case 2:
                    
                    vm.tabHeaderText = 'Communication Details';
                    vm.isCommunicationVisible = true;
                    vm.GetConfiguration = { user_id: vm.userId, user_name: vm.Headerstext };
                    break;
                case 3:
                    
                    vm.tabHeaderText = 'Addresses';
                    vm.isAddressVisible = true;
                    break;
                case 4:
                    vm.isShowContactNumber = true;
                    vm.tabHeaderText = 'Contact Numbers';
                    break;
                 case 5:
                    vm.tabHeaderText = 'Team Structure';
                    vm.isTeamStructureVisible = true;
                    break;
                 
                case 6:
                    vm.tabHeaderText = 'Security Questions';
                    vm.isSecurityQuestions = true;
                    break;
                case 7:
                    vm.tabHeaderText = 'Face Capture';
                    vm.showFaceRecognizerModal = true;
                    break;
                default:
                    break;
            }
        
        },
        receiveUserTypeId(userTypeid, userName) {
            var vm = this;
            vm.userType = '';
            vm.userType = parseInt(userTypeid);
            vm.Headerstext = userName;
        },
        async GetPasswordPolicyByCompanyId(){
            var vm = this;
            await DataService.GetPasswordPolicyByCompanyId().then(response=>{
                if(response.data != "" && response.data != null && response.data != undefined){
                    if(response.data.is_secret_question_enable==true)
                    {
                        vm.tabs.push({
                        title: "Security Questions",
                        icon: "fa fa-question",
                        id: 6
                    })
                    }
                   
                }
            })
        },
    }
}
</script>
