<script>
import DataService from '../../services/DataService';
import modalUserResetPasswordVue from '../../components/Modals/modalUserResetPassword.vue';
import authService from "../../services/authservice";
export default {
    components: {
        modalUserResetPasswordVue
    },
    data() {
        return {
            userType: '',
            Userdetail: {},
            UserResetPasswordData: {
                user_id: this.GetUserInfo.ID,
                email_id: this.GetUserInfo.Email
            },
            ShowUserResetPassword: false,
            // IsSalesForceEnableWebConfigKey: process.env.Is_Sale_force_Enable,
            UserTitle: [],
            IsTimerOn: false,
            TimeZoneList: [],
            isUSICcheck: false,
            TipOfTheDay: false,
            FlagNotificationCheck:this.GetUserInfo.FlagNotificationCheck?"true":"false",
            status:'',
            profilePicture:'',
            gender:'',
            containerName:'',
            attachmentName:'',
            base64String:'',
            fileData: [],
            fileInputKey: 0,
             errors: {
                firstName: false,
                lastName: false,
                email:false
            },
            checkexistemail:false,
            UserId:this.GetUserInfo.ID,
        };

    },
    created() {
        this.GetUserInfo.DoNotShowDirtyCheck = this.GetUserInfo.DoNotShowDirtyCheck === "True";
        this.GetUserInfo.FlagNotificationCheck=this.GetUserInfo.FlagNotificationCheck?"true":"false",
        this.userType = this.GetUserInfo.UserType;
        this.GetUserProfile();
        this.IsSendDeactivationRequest();
        this.GetUserTitleByCompanyId();
        this.GetTimeZone();
        this.GetSubscribeStatusByUserId();
        this.CheckExtraFeatures();
        this.TimerOn();
        
    },
    methods: {
        async CheckExtraFeatures() {
            const params = `featureCode=USICBILLING&outputType=list`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    this.isUSICcheck = response.data.toUpperCase() === "TRUE";
                }
            } catch (error) {
                console.error("Error checking extra features:", error);
            }
        },
        async GetUserProfile() {
            //debugger;
            var vm = this;
            DataService.GetUserProfile().then(response => {
                //debugger;
                if (response && response.data) {
                    vm.Userdetail = { ...response.data }; // Assign the API response to Userdetail
                    vm.userType = this.Userdetail.userType || ''; // Ensure userType is set
                    vm.profilePicture = this.Userdetail.avatar;
                    vm.containerName = this.Userdetail.containerName;
                    vm.attachmentName = this.Userdetail.avatar;
                    vm.gender=this.Userdetail.gender
                }

            })
        },
        async GetUserTitleByCompanyId() {
            var vm = this;
            DataService.GetUserTitleByCompanyId().then(response => {
                if (response && response.data) {
                    vm.UserTitle = response.data;
                }

            })
        },
        async TimerOn() {
            var vm = this;
            DataService.TimerOn().then(response => {
                if (response && response.data) {
                    if (response.data == 1) {
                        vm.IsTimerOn = true
                    }

                }

            })
        },
        GetTimeZone() {
            var vm = this;
            DataService.GetTimeZoneList().then(function (response) {
                if (response && response.data) {
                    vm.TimeZoneList = response.data;
                }

            });
        },
        CloseUserResetPasswordPopUp() {
            var vm = this;
            vm.ShowUserResetPassword = false;
        },
        async GetSubscribeStatusByUserId() {
            var vm = this;
            DataService.GetSubscribeStatusByUserId().then(response => {
                if (response && response.data) {
                    vm.TipOfTheDay = response.data
                }

            })
        },
        async IsSendDeactivationRequest(){
         var vm = this;
            DataService.IsSendDeactivationRequest().then(response => {
                if (response && response.data) {
                    vm.status = response.data
                }

            })
        },
        handleChange(type) {
            var vm = this;
            
            if(type=='DirtyCheck'){
              if(this.GetUserInfo.DoNotShowDirtyCheck){
                    this.GetUserInfo.DoNotShowDirtyCheck=true
               }
                    else{
                    this.GetUserInfo.DoNotShowDirtyCheck=false
                    }
            }
            else if(type=='FlagNotificationCheck'){
                if (this.GetUserInfo.FlagNotificationCheck) {
                    this.GetUserInfo.FlagNotificationCheck = true;
                }
                else {
                   this.GetUserInfo.FlagNotificationCheck = false;
                }
            }
            else{
                 if (vm.TipOfTheDay) {
                    vm.TipOfTheDay = true;
                }
                else {
                   vm.TipOfTheDay = false;
                }
            }
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];
                var files = evt.target.files; // FileList object
                vm.attachmentName = files[0].name;
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String = base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            //vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            //vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            //vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.GetUserInfo.ID,
                                companyId: vm.GetUserInfo.CompanyId,
                                id: 0,
                                moduleName: "initialsetup",
                                subModuleCode: "",
                                containerName: vm.GetUserInfo.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path: ''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
        ResetProfileImage() {
            var vm = this;
            vm.fileData = [];
            vm.attachmentName = '';
            vm.base64String = '';
            vm.profilePicture = '';
            vm.fileInputKey += 1;
        },
       async SaveSubmit(btnType){
        //debugger;
            var vm = this;
                this.errors.firstName = !this.Userdetail.firstName;
                this.errors.lastName = !this.Userdetail.lastName;
                this.errors.email=!this.Userdetail.email;
                if (!this.errors.firstName && !this.errors.lastName && !this.errors.email) {
                    
                    await vm.CheckEmailExist(this.Userdetail.email);                
                        if(vm.checkexistemail){
                            vm.ShowAlert(vm.$t("EmailExists"), 'failure', true, vm.$t('Alert'));
                                
                                return;
                        }
                    vm.isLoading = true;              
                    if(btnType!='deleteimg'){
                            await vm.UploadProfilePicture();
                      }
                     
                    var jsonObj = {
                            user_title_id: vm.Userdetail.userTitleId,
                            first_name: vm.Userdetail.firstName,
                            middle_name: vm.Userdetail.middleName,
                            last_name: vm.Userdetail.lastName,
                            gender: vm.Userdetail.gender,
                            email_id: vm.Userdetail.email,
                            time_zone_id: vm.Userdetail.timezoneId ? vm.Userdetail.timezoneId : null,
                            time_format: vm.Userdetail.timeofDayFormat,
                            profile_path: vm.profilePicture,
                            user_config: {
                                    DoNotShowDirtyCheck: vm.GetUserInfo.DoNotShowDirtyCheck,
                                    EnableMyDashboard: vm.GetUserInfo.EnableMyDashboard,
                                    FlagNotificationCheck: vm.GetUserInfo.FlagNotificationCheck,
                                    // tip_of_the_day: vm.TipOfTheDay
                                }
                            
                    }
                     var objectPostString = JSON.stringify(jsonObj);
                     await DataService.SaveUpdateUserProfile(objectPostString).then(function (response) {
                            if (response.data && response.data.toLowerCase() == 'success') {
                           
                                if(btnType!='deleteimg'){
                                    vm.ShowAlert(vm.$t('UpdatedSuccess',true,[vm.$t('User')]), 'success', true, vm.$t('Alert'));
                                }
                                authService.signIn(vm.$route.path).then(() => {});
                                vm.GetUserProfile();
                                
                            }
                        })
                            vm.isLoading = false; 
                    }
        },
        async CheckEmailExist(email){
            //debugger;
            var vm = this;
            var getUser = 0;
            if(vm.UserId != undefined){
                getUser = vm.UserId;
            }
            var param = `emailId=${email}&userId=${getUser}`;
            await DataService.CheckEmailExist(param).then(response=>{
                if(response.data == true){
                    vm.checkexistemail = true;
                }else{
                    vm.checkexistemail = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        async UploadProfilePicture() {
            var vm = this;
            var formData = JSON.stringify(vm.fileData);
            await DataService.UploadFile(formData).then(function (response) {
                if (response.data && response.data.length > 0) {
                //debugger;
                    if (response.data[0].Name.length > 0) {
                        var img = response.data[0].Name.split('/')[response.data[0].Name.split('/').length - 1]
                        vm.profilePicture = img;
                    }
                }
            })
        },
        DeleteSampleData(){
             var vm=this;
            vm.confirmR(vm.$t("ConfirmDeleteSampleData"), true, (vm.$t("Delete") + "  " + vm.$t("AddtionalPhone")), function () {
                DataService.DeleteSampleData().then(response=>{   
                    if(response && response.data){
                        //debugger
                        if(response.data.msg=='success'){
                            vm.ShowAlert(vm.$t('SampleDataDeleted'), 'success', true, vm.$t('Alert'));       
                        }
                        else{
                              vm.ShowAlert(vm.$t('Something_Wrong'), 'failure', true, vm.$t('Alert'));
                            }
                    }
                })
            })
        },
        async DeleteProfileImage(){
            //debugger;
            var vm=this;
          vm.confirmR(vm.$t("ConfirmProfileImage"), true, (vm.$t("Delete") + "  " + vm.$t("AddtionalPhone")), function () {
        
                DataService.DeleteProfileImage().then(function (response) {
               // debugger
                   if(response.data=='success'){
                        vm.attachmentName='';
                        vm.profilePicture=null;
                        vm.SaveSubmit('deleteimg');
                    vm.ShowAlert(vm.$t('ProfileImage')+' '+vm.$t('remove'), 'success', true, vm.$t('Alert')); 
                     authService.signIn(vm.$route.path).then(() => {});
                   } 
                    else{
                     vm.ShowAlert(vm.$t('Something_Wrong'), 'failure', true, vm.$t('Alert'));
                    }             

                })                  
            })    

        },
         redirectToConfig() {
            this.$router.push('/Configuration/index');
        },
        signIn (returnToUrl) {
        returnToUrl ? this.mgr.signinRedirect({ state: returnToUrl })
            : this.mgr.signinRedirect();
        }

    }
}
</script>
