<template>
    <section>
     <div class="right-content right-content-expand border p-3">
        <div class="col-md-12 p-0">
            <div class="tab-content"></div>
                <form @submit.prevent="submitForm" id="frmSecretAnswersDetail" autocomplete="off" role="form">
                <div v-if="userGuide" class="user-guide-custom">
                    <div v-html="userGuide"></div>
                </div>
                <div class="row">
                    <div v-for="(item, index) in secretAnswers" :key="index" class="col-12 d-flex flex-wrap">
                    <input type="hidden" v-model="item.secret_answer_id" />
                    <div class="col-sm-12 col-lg-5">
                        <div class="form-group">
                        <label>{{$t('Question')}}</label>
                        <select v-model="item.secret_question_id" :id="'secretquestionid_' + index" class="form-control SecretQuestion" required  @change="validateUniqueQuestions">
                            <option value="">Select a question</option>
                            <option v-for="question in questionList" :key="question.secret_question_id" :value="question.secret_question_id">
                            {{ question.secret_question_name }}
                            </option>
                        </select>
                        </div>
                    </div>
                    <div class="col-sm-12 col-lg-7">
                        <div class="form-group">
                        <label>{{$t('Answer')}}</label>
                        <input 
                            type="text" 
                            v-model="item.secret_answer_name" 
                            :id="'secretanswerid_' + index" 
                            class="form-control SecretAnswer" 
                            maxlength="100" 
                            required
                        />
                        </div>
                    </div>
                    </div>
                    <div class="col-lg-12 text-right mt-3">
                    <button type="submit" class="btn btn-success" title="Save">
                        <i class="fa fa-save pr-2"></i>Save
                    </button>
                    </div>
                </div>
                </form>
            </div>
        </div>
    </div>
</section>
  </template>
  
  <script>
   import DataService from '../../services/DataService';
  export default {
    data() {
      return {
        secretAnswers: [],
        questionList: [],
        duplicateQuestions: []
      };
    },
    async created(){
        var vm=this;
        await vm.GetQuestionList();
        await vm.GetSecretAnswers();
    },
    methods: {
        async GetQuestionList(){
            var vm=this;
            const response = await DataService.GetQuestionList();
              if (response.data && response.data.result) {
                  vm.questionList=response.data.result
               } 
        },
        async GetSecretAnswers(){
            var vm=this;
            const response = await DataService.GetSecretAnswers();
              if (response.data && response.data.result) {
                  vm.secretAnswers=response.data.result
               } 
        },
        validateUniqueQuestions() {
            const selectedQuestions = this.secretAnswers.map(item => item.secret_question_id);
            this.duplicateQuestions = selectedQuestions.filter((item, index) => selectedQuestions.indexOf(item) !== index);
            if(this.duplicateQuestions.length>0){
                this.ShowAlert("This question is already selected.Please select another question", "failure", this.$t('Alert'));
            }
        },
        async submitForm() {
            let valid = true;
            debugger
            this.secretAnswers.forEach(item => {
            if (!item.secret_question_id) {
                this.ShowAlert("Please select all security questions.", "failure", this.$t('Alert'));
                valid = false;
            }
            if (!item.secret_answer_name) {
                this.ShowAlert("All security answers are required.", "failure", this.$t('Alert'));
                
                valid = false;
            }
            });
            
            if (!valid) return;
            
            try {
                let Jsondata = JSON.stringify(this.secretAnswers)
                const response = await DataService.saveSecretAnswer(Jsondata);
            if (response.data==true) {
                this.ShowAlert("Security answers saved successfully!", "success", this.$t('Alert'));
                await this.GetQuestionList();
                await this.GetSecretAnswers();
            } else {
                this.ShowAlert("Failed to save security answers.", "failure", this.$t('Alert'));
            }
            } catch (error) {
            console.error("Error saving security answers:", error);
            this.ShowAlert("An error occurred while saving.", "failure", this.$t('Alert'));
            }
        }
  
      }
   
  };
  </script>
  